import React from "react";
// import sections
import Hero from "../components/sections/Hero";
import FeaturesTiles from "../components/sections/FeaturesTiles";
// import Testimonial from "../components/sections/Testimonial";
// import Cta from "../components/sections/Cta";
import FAQs from "../components/sections/FAQs";
import WhyUs from "../components/sections/WhyUs";
import SectionHeader from "../components/sections/partials/SectionHeader";
import Personalized from "../components/sections/Personalized";
import ButtonGroup from "../components/elements/ButtonGroup";
import Button from "../components/elements/Button";
import Savings from "../components/sections/Savings";

const Home = () => {
  return (
    <>
      <Hero className="illustration-section-01" />

      <FeaturesTiles className="bg-light-blue" />

      <Personalized />
      <WhyUs
        invertMobile
        imageFill
      />

      <Savings />

      <SectionHeader
        tag="h3"
        data={{ title: <>Whether you are from Madurai or from Melbourne, as long as you are <span className="text-color-primary">comfortable in english</span>, we highly encourage you to <span className="text-color-primary">join our health Club!</span></> }}
        className="center-content"
        titleClass="text-color-secondary"
      />

      {/* <Testimonial /> */}

      <FAQs />

      <SectionHeader
        // tag="h2"
        data={{ title: "It all starts with a single step" }}
        className="center-content"
        titleClass="text-color-secondary"
      />
      <div className="reveal-from-bottom center-content">
        <ButtonGroup>
          {/* <Button tag="a" color="primary" wideMobile href="#waitlist">
            Join the waitlist
                  </Button> */}
          <Button tag="a" color="secondary" wideMobile href="https://wa.me/919625134457?text=Hey! I am interested in 7Confirmations, can I know more about it?"
            target="_blank"
            rel="noopener noreferrer">
            Start on Whatsapp
                  </Button>
        </ButtonGroup>
      </div>
      {/* <Cta split invertColor /> */}
    </>
  );
};

export default Home;
