import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { HashLink as Link } from 'react-router-hash-link';

import Logo from './partials/Logo';
// import FooterNav from './partials/FooterNav';
import FooterSocial from './partials/FooterSocial';
// import Cta from '../sections/Cta'
import Button from '../elements/Button';


const propTypes = {
  topOuterDivider: PropTypes.bool,
  topDivider: PropTypes.bool
}

const defaultProps = {
  topOuterDivider: false,
  topDivider: false
}

const Footer = ({
  className,
  topOuterDivider,
  topDivider,
  ...props
}) => {

  const classes = classNames(
    'site-footer center-content-mobile',
    topOuterDivider && 'has-top-divider',
    className
  );

  return (
    <footer
      {...props}
      className={classes}
    >
      <div className="container">
        <div className={
          classNames(
            'site-footer-inner',
            topDivider && 'has-top-divider'
          )}>
          <div className="footer-top space-between text-xxs">
            {/* <Logo />
            <FooterSocial /> */}
            <div className="footer-blocks">

              {/* <!-- Block --> */}
              <div className="footer-block">
                <div className="brand mb-16">
                  {/* <a href="#"> */}
                  {/* <!-- Site name, or image (ideal size: 32x32px) --> */}
                  <Logo />
                  {/* </a> */}
                </div>
                {/* <div className="footer-copyright">
                  7-Confirmations
                </div> */}
              </div>


              {/* <!-- Block with links --> */}
              <div className="footer-block">
                <div className="footer-block-title">Company</div>
                <ul className="list-reset">

                  <li>
                    <Link smooth to="#features" >
                      Features
                      </Link>
                  </li>
                  <li>
                    <Link smooth to="#personalized" >
                      Personalized for You
                      </Link>
                  </li>
                  <li>
                    <Link smooth to="#why-us" >
                      Why Us?
                      </Link>
                  </li>
                  <li>
                    <Link smooth to="#savings" >
                      Savings
                      </Link>
                  </li>

                </ul>
              </div>

              {/* <!-- Block with links --> */}
              <div className="footer-block">
                <div className="footer-block-title">Resources</div>
                <ul className="list-reset">

                  <li>
                    <Link smooth to="#faqs" >
                      FAQ's
                      </Link>
                  </li>
                  <li>
                    <a href="/privacy-policy">Privacy Policy</a>
                  </li>
                  <li>
                    <a href="/terms-conditions">Terms and Conditions</a>
                  </li>
                  <li>
                    <a
                      href="https://medium.com/@7confirmations"
                      target="_blank"
                      rel="noopener noreferrer"

                    >
                      Blog
                      </a>
                  </li>
                </ul>
              </div>


              <div className="footer-block">
                <div className="footer-block-title">Curious to learn more?</div>
                <Button tag="a" color="secondary" wideMobile href="https://wa.me/919625134457?text=Hey! I am interested in 7Confirmations, can I know more about it?"
                  target="_blank"
                  rel="noopener noreferrer">
                  Start on Whatsapp
                  </Button>

              </div>


            </div>
          </div>
          <div className="footer-bottom space-between text-xxs invert-order-desktop">
            <FooterSocial />
            <div className="footer-copyright">All rights reserved. 7-Confirmations &copy; {new Date().getFullYear()}</div>
          </div>
        </div>
      </div>
    </footer >
  );
}

Footer.propTypes = propTypes;
Footer.defaultProps = defaultProps;

export default Footer;