import React from "react";
import classNames from "classnames";
import { SectionProps } from "../../utils/SectionProps";
import ButtonGroup from "../elements/ButtonGroup";
import Button from "../elements/Button";
import Image from "../elements/Image";
// import Modal from "../elements/Modal";

const propTypes = {
  ...SectionProps.types,
};

const defaultProps = {
  ...SectionProps.defaults,
};

const Hero = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,

  imageFill,
  invertMobile,
  invertDesktop,
  alignTop,
  ...props
}) => {
  // const [videoModalActive, setVideomodalactive] = useState(false);

  // const openModal = (e) => {
  //   e.preventDefault();
  //   setVideomodalactive(true);
  // };

  // const closeModal = (e) => {
  //   e.preventDefault();
  //   setVideomodalactive(false);
  // };

  const outerClasses = classNames(
    "hero section center-content",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "hero-inner section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider",
    "split-item"
  );

  const splitClasses = classNames(
    "split-wrap",
    invertMobile && "invert-mobile",
    invertDesktop && "invert-desktop",
    alignTop && "align-top"
  );

  return (
    <section {...props} className={outerClasses}>
      <div className="container">
        <div className={splitClasses}>
          <div className={innerClasses}>
            <div
              className="hero-content split-item-content center-content-mobile reveal-from-left"
              data-reveal-container=".split-item">
              <h1
                className="mt-0 mb-16 reveal-from-bottom"
                data-reveal-delay="200"
              >
                World first solution to{" "}
                <span className="text-color-primary">PCOD</span> and{" "}
                <span className="text-color-primary">PCOS*</span>
              </h1>
              <div className="container-xs">
                <p
                  className="m-0 mb-32 reveal-from-bottom"
                  data-reveal-delay="400"
                >
                  Whether your target is to conceive naturally or to get rid of
                  those unwanted hairs, to regularize your menses or to fit well
                  in your black dress, we welcome you to the club!!
              </p>
                <div className="reveal-from-bottom" data-reveal-delay="600">
                  <ButtonGroup>
                    {/* <Button tag="a" color="primary" wideMobile href="#waitlist">
                      Join the waitlist
                  </Button> */}
                    <Button tag="a" color="secondary" wideMobile href="https://wa.me/919625134457?text=Hey! I am interested in 7Confirmations, can I know more about it?"
                      target="_blank"
                      rel="noopener noreferrer">
                      Start on Whatsapp
                  </Button>
                  </ButtonGroup>
                </div>
              </div>
            </div>
            <div

              className={classNames(
                "hero-figure reveal-from-bottom illustration-element-01",
                "split-item-image center-content-mobile reveal-from-bottom",
                "split-item-image-fill"
              )}
              data-reveal-container=".split-item"

              // className="hero-figure reveal-from-bottom illustration-element-01"
              data-reveal-value="20px"
              data-reveal-delay="800"
            >
              {/* <a
              data-video="https://player.vimeo.com/video/174002812"
              href="#0"
              aria-controls="video-modal"
              onClick={openModal}
            > */}
              <Image
                className="has-shadow"
                src={require("./../../assets/images/hero-image.png")}
                alt="Hero"
                width={896}
                height={504}
              />
              {/* </a> */}
            </div>
            {/* <Modal
            id="video-modal"
            show={videoModalActive}
            handleClose={closeModal}
            video="https://player.vimeo.com/video/174002812"
            videoTag="iframe"
          /> */}
          </div>

        </div>


      </div>
    </section>
  );
};

Hero.propTypes = propTypes;
Hero.defaultProps = defaultProps;

export default Hero;
